@import "../../Mixins";

.nav {
  padding-top: 80px;
  max-width: 1146px;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  max-height: 100vh;

  @include display( grid );
  @include grid-template-columns( 1fr 2px 1fr );
  @include grid-template-rows( 100vh ); }

.loader {
  @include position( absolute );
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  @include display( flex );
  @include align-items( center );
  @include justify-content( center ); }

.border {
  width: 100%;
  height: 100vh;
  background-color: #000; }
